<template>
    <div id="app" class='bg wholepage _fixed'>
        <transition :enter-active-class="enterActiveClass" :leave-active-class="leaveActiveClass">
            <router-view></router-view>
        </transition>
        <play-list ref='playList'></play-list>
        <footer-play-bar></footer-play-bar>
    </div>
</template>
<script>
import footerPlayBar from './components/footer';
import playList from './components/play-list'
import {
    bg
} from './config/skin-config.js'
(function(doc, win) {
    let docEl = doc.documentElement;
    let clientHeight = docEl.clientHeight;
    docEl.style.fontSize = clientHeight / 100 * 4 + 'px';
    // let style=doc.createElement('style');
    // style.innerHTML='.bg{height:'+clientHeight+'px}'
    // doc.head.appendChild(style);
})(document, window)
export default {
    name: 'app',
    data() {
        return {
            bg: `url(${bg})`,
            leaveActiveClass:'',
            enterActiveClass:''
        }
    },
    components: {
        footerPlayBar,
        playList
    },
    watch: {
        '$route' (to, from) {
            const toDepth = to.path.split('/').length
            const fromDepth = from.path.split('/').length
        //    this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
            if(toDepth>fromDepth){
                this.enterActiveClass='animated slideIn animated-in';
                this.leaveActiveClass='animated thirdSildeOut animated-in'
            }else{
                this.enterActiveClass='animated thirdSildeIn animated-in';
                this.leaveActiveClass='animated slideOut animated-in'
            }
        }
    }
}
</script>
<style lang='scss'>
@import './styles/lv/index.scss';

$border_color       : #195275;
$img_width          : 50px;
$color_light        : #99B6C3;
$color_primary      : #EEF2F5;
$playholder_color   : #505460;
$light_color_2      : #00D1FF;
$light_active_color : #FBF6A9;
$nav_bg_color       : #213051;

/*::-webkit-scrollbar {
     width: 2px;
}
*/
.header-con{
    display: flex;
    box-sizing: border-box;
    margin-top: 0.25rem;
    padding:0.25rem 3vw;
    height: 1.5rem;
    *{
        line-height: 1.15rem;
        height: 1.15rem;
    }
    .iconfont{
        font-size: 5vw;
    }
    .icon-left{
        margin-right: 4vw;
        position: relative;
    }
} 

.mint-toast{
    font-weight: 200;
}
.mint-msgbox-header{
    display: none;
}
body .mint-msgbox-message{
    color:$color_primary;
    line-height: 2.2rem
}
body .mint-msgbox{
    font-size: 4vw;
}
.mint-msgbox-btns button{
    font-size: 4.5vw;
    background-color: transparent;
}
body button.mint-msgbox-cancel{
    border-right: 1px solid $border-color;
    color:$color_primary;
    &:active{
        background-color:transparent;
        color:color_primary;
    }
}
body button.mint-msgbox-confirm{
    &:active{
        background-color:transparent;
    }
}
body .mint-msgbox-content{
    border-bottom: 1px solid $border-color;
}
.animated.animated-in {
    animation-duration: .5s;
}

.active-border{
    color:$light_active_color;
    height:2px;
    background-color:$border_color; 
}

.active{
    color:$light_active_color !important;
}

.swipe-con-nav{
    background-color: $nav_bg_color
}
#app {
    font-family: 'microsoft yahei', 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 4vw;
    ul,
    li,
    div {
        border-color: $border_color;
    }
    width:100vw;
    /* overflow:hidden; bug  */
    @for $i from 1 through 6 {
        .z#{$i} { z-index:$i; }
    }
}

.text-no-wrap {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.small {
    font-size: 3vw;
    line-height: 1.2;
}

.medium{
    font-size: 4.5vw;
    line-height: 1.3;
}
.iconfont {
    display: inline-block;
}

.wholepage {
    height: 25rem;
    width: 100vw;
}

.view {
    position: absolute;
    left: 0px;
    right: 0px;
}

.bg {
    box-sizing: border-box;
    width: 100vw;
    height: 25rem;
    background-size: cover;
}

.light_color_2 {
    color: $light_color_2;
}

.light_color {
    color: $color_light;
}

.primary_color {
    color: $color_primary;
}

.btn-noborder {
    border: none;
    color: $color_primary;
    background: transparent;
    outline: none;
}

input {
    outline: none;
}

._right {
    float: right;
}

._relative {
    position: relative;
}

._absolute {
    position: absolute;
}

._fixed {
    position: fixed;
}

.ripple-container {
  
}

.ripple {
    background-color: rgba(255, 255, 255, 0.4);
    animation: ripple .8s forwards cubic-bezier(0, 0, 0.2, 1);
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
    color: $playholder_color;
}

input:-moz-placeholder,
textarea:-moz-placeholder {
    color: $playholder_color;
}

input::-moz-placeholder,
textarea::-moz-placeholder {
    color: $playholder_color;
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
    color: $playholder_color;
}

@keyframes ripple {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    80% {
        transform: scale(1);
    }
    100% {
        opacity: 0;
    }
}

@-webkit-keyframes slideOut {
    from {
        -webkit-transform: none;
        transform: none;
    }
    to {
        -webkit-transform: translate3d(100vw, 0, 0);
        transform: translate3d(100vw, 0, 0);
    }
}

@keyframes slideOut {
    from {
        -webkit-transform: none;
        transform: none;
    }
    to {
        -webkit-transform: translate3d(100vw, 0, 0);
        transform: translate3d(100vw, 0, 0);
    }
}

@-webkit-keyframes slideIn {
    from {
        -webkit-transform: translate3d(100vw, 0, 0);
        transform: translate3d(100vw, 0, 0);
    }
    to {
        -webkit-transform: none;
        transform: none;
    }
}

@keyframes slideIn {
    from {
        -webkit-transform: translate3d(100vw, 0, 0);
        transform: translate3d(100vw, 0, 0);
    }
    to {
        -webkit-transform: none;
        transform: none;
    }
}

@-webkit-keyframes thirdSildeOut {
    from {
        -webkit-transform: none;
        transform: none;
        
    }
    to {
        -webkit-transform: translate3d(-33vw, 0, 0);
        transform: translate3d(-33vw, 0, 0);
    }
}

@keyframes thirdSildeOut {
    from {
        -webkit-transform: none;
        transform: none;
        
    }
    to {
        -webkit-transform: translate3d(-33vw, 0, 0);
        transform: translate3d(-33vw, 0, 0);
    }
}

@-webkit-keyframes thirdSildeIn {
    from {
        -webkit-transform: translate3d(-33vw, 0, 0);
        transform: translate3d(-33vw, 0, 0);
    }
    to {
        -webkit-transform: none;
        transform: none;
    }
}

@keyframes thirdSildeIn {
    from {
         -webkit-transform: translate3d(-33vw, 0, 0);
        transform: translate3d(-33vw, 0, 0);
    }
    to {
        -webkit-transform: none;
        transform: none;
    }
}

.slideIn{
    -webkit-animation-name: slideIn;
    animation-name: slideIn;
}

.slideOut{
    -webkit-animation-name: slideOut;
    animation-name: slideOut;
}

.thirdSildeOut{
    -webkit-animation-name: thirdSildeOut;
    animation-name: thirdSildeOut;
}

.thirdSildeIn{
    -webkit-animation-name: AutoFileName;
    animation-name: thirdSildeIn;
}
</style>



